.body {
  /* border: 1px solid red; */
  flex: 0.75;
  background: linear-gradient(rgb(91, 87, 115), rgba(0, 0, 0, 1));
  color: white;
  padding: 30px;
  overflow-y: overlay;
}

.body::-webkit-scrollbar {
  display: none;
}

.body__info {
  display: flex;
  align-items: flex-end;
  /* justify-content: space-between; */
  margin-top: 30px;
}

/* .body__infoText {
  flex: 1;
} */
.body__info img {
  height: 20vh;
  /* width: 20vw; */
  box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
  margin: 0 20px;
}
.body__infoText h2 {
  font-size: 48px;
  margin-bottom: 10px;
}

.body__infoText p {
  font-size: 14px;
}

.body__songs {
  margin: 20px -30px;
}
.body__icons {
  display: flex;
  align-items: center;
}

.body__shuffle {
  font-size: 80px !important;
  margin: 20px 0 20px 50px;
}

.body__shuffle:hover {
  transition: 100ms transform ease-in;
  transform: scale(1.08);
}

.body__icons .MuiSvgIcon-root {
  margin-right: 20px;
}
